import React from 'react'

import { StaticQuery, graphql } from 'gatsby'
import { NonStretchedImage } from "actl"


export default function Image(){
    return (<StaticQuery
        query={graphql`
            query {
                img: file(relativePath: { eq: "pipeline.png" }) {
                    childImageSharp {
                        fluid(maxWidth: 400) {
                        ...GatsbyImageSharpFluid
                        presentationWidth
                        }
                    }
                }
            }
        `}
        render= {data => <NonStretchedImage {...data.img.childImageSharp}/>}
    />)
}